import React, {
  useState,
  ReactNode,
  useEffect,
  FC
} from 'react';

type sizes = 'xs' | 'sm' | 'md' | 'lg' | 'xl';

type radius = 'none' | 'rounded' | 'pill';

export interface ButtonProps {
  children: ReactNode;
  onClick?: () => void;
  size?: sizes;
  color?: string;
  radius?: radius;
  icon?: ReactNode;
  style?: React.CSSProperties;
}

const Button: FC<ButtonProps> = ({
  children,
  size = 'sm',
  radius = 'rounded',
  color = 'blue',
  icon,
  onClick,
  style,
}: ButtonProps) => {

  const buttonSize =
    size === 'xs'
      ? {
        height: 30,
        fontSize: 12,
        }
          : size === 'sm'
          ? {
            height: 40,
            fontSize: 20,
            }
          : size === 'md'
            ? {
              height: 45,
              fontSize: 30,
              }
            : size === 'lg'
              ? {
                height: 50,
                fontSize: 40,
                }
              : {
                height: 55,
                fontSize: 50,
                };

  const radiusValue =
    radius === 'none'
      ? 0
      : radius === 'rounded'
        ? 8
        : 30

  const defaultStyles = {
    border: '1px solid #ccc',
    borderRadius: radiusValue,
    padding: '5px 20px',
    textAlign: 'center',
    wrap: 'nowrap',
    width: 'auto',
    color: 'white',
    backgroundColor: color,
    ...buttonSize,
    ...style,
    } as React.CSSProperties;

  return (
    <button
      onClick={onClick}
      style={defaultStyles}
    >
      {icon}
      {children}
    </button>
  )
}

export default Button;
